body {
  margin: 0;
  padding: 10px;
  background-color: rgb(92, 91, 91) !important;
}
.VideoWrapper {
  height: 75vh;
  width: auto;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content:space-between;
  padding: 10px;
}

#uplContainer{
  display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
    height: 100%;
}

#VideoCard{
  align-items: center;
  width: 320px;
  height: 500px;
  padding: 10px;
  background-color: gray;
}
#navBtnsCnt{
  width: 550px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 450px) { /*MOBILE SECTION*/
  body {
  margin: 0;
  padding: 10px;
  background-color: rgb(115, 115, 115) !important;
}

  #navBtnsCnt{
    position: relative;
    display: flex;
    justify-content:space-between;
    width: 350px;
    height: 47px;
    margin: 5px;
  }
  #VideoCard{
    top: 25px;
    align-items: center;
    width: 450px;
  }
  #ListItem{
    position: relative;
    display: flex;
    justify-content: space-around;
    top: 38px;
    width: 400px;
    margin: 10px;
    
  }
  #paginCnt{
    position: relative;
    top: 40px;
  }
  #titleBtn{
    width:100px;
    overflow:hidden;
    height:40px;
    line-height:40px;
  }
}
